@import url('https://fonts.googleapis.com/css2?family=Zen+Kurenaido&display=swap');
body{
    background-color: black;
    display: flex;
    justify-content: center;
}
.form{
    background-color: #fff;
    width: 300px;
    height: 220px;
    padding-left: 80px;
    padding-top: 50px;
}
.input-email{
    display: block;
    margin-bottom: 20px;
    width: 222px;
    height: 39px;
    background-color: black;
    color: #fff;
    font-size: 24px;
    padding-left: 10px;
    font-family: 'Zen Kurenaido', sans-serif;
    border: none;
    border-radius: 2px;
    margin-top: 33px;
}
.input-password{
    display: block;
    margin-bottom: 20px;
    width: 222px;
    height: 39px;
    background-color: black;
    color: #fff;
    font-size: 24px;
    padding-left: 10px;
    border-radius: 2px;
    font-family: 'Zen Kurenaido', sans-serif;
    border: none;
}
.input-password:focus{
    outline: none;
}
.input-email:focus{
    outline: none;
}
.input-email::placeholder{
    color: #fff;
    font-size: 24px;
    padding-left: 10px;
}
.input-password::placeholder{
    color: #fff;
    font-size: 24px;
    padding-left: 10px;
}
.btn{
    margin-left: 50px;
    font-family: 'Zen Kurenaido', sans-serif;
    width: 145px;
    height: 38px;
    border: 2px solid black;
    cursor: pointer;
    color: #fff;
    background-color: black;
    border-radius: 2px;
    font-size: 18px;
}
.btn:hover{
    background-color: transparent;
    color: black;
    transition: 1s ease all;
}
.title{
    font-family: 'Zen Kurenaido', sans-serif;
    position: absolute;
    margin-top: -44px;
    margin-left: 70px;
}
.email-x{
    color: #fff;
    display: none;
    position: absolute;
    margin-top: 4px;
    margin-left: 205px;
}
.email-ptichka{
    position: absolute;
    display: none;
    color: #fff;
    margin-left: 205px;
    margin-top: 3px;
}
.password-x{
    color: #fff;
    display: none;
    position: absolute;
    margin-top: -118px;
    margin-left: 205px;
}
.password-ptichka{
    position: absolute;
    display: none;
    color: #fff;
    margin-left: 205px;
    margin-top: -118px;
}
.text{
    font-family: 'Zen Kurenaido', sans-serif;
    margin-top: -1px;
    margin-left: 10px;
    font-size: 18px;
    position: absolute;
    display: none;
}